:root {
  --f-body: "Montserrat", sans-serif;
  --c-primary: #292929;
  --c-secondary: #da002e;
  --c-heading: #000000;
  --c-text: #4A4B4C;
  --c--background: #F5F6F7;
}

body {
  background-color: white;
  line-height: 1.6;
  font-family: var(--f-body);
  font-weight: 400;
  font-size: 17px;
  color: #4A4B4C;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

.container {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    max-width: 100% !important;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 980px !important;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px !important;
  }
}
@media (min-width: 1420px) {
  .container {
    max-width: 1360px !important;
  }
}
@media (min-width: 1640px) {
  .container {
    max-width: 1500px !important;
  }
}

.site-main,
.site-main > .post {
  margin-top: 150px;
}
.site-main > *,
.site-main > .post > * {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  margin-bottom: 1.5rem;
}
.site-main > *:not(.alignwide):not(.alignfull):not(.post),
.site-main > .post > *:not(.alignwide):not(.alignfull):not(.post) {
  max-width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 992px) {
  .site-main > *:not(.alignwide):not(.alignfull):not(.post),
.site-main > .post > *:not(.alignwide):not(.alignfull):not(.post) {
    max-width: 980px;
  }
}
.site-main > *:not(.alignfull) .container,
.site-main > .post > *:not(.alignfull) .container {
  padding-left: 0;
  padding-right: 0;
}
.site-main > *.alignwide,
.site-main > .post > *.alignwide {
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 100% !important;
  }
}
@media (min-width: 992px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 980px !important;
  }
}
@media (min-width: 1200px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 1140px !important;
  }
}
@media (min-width: 1420px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 1360px !important;
  }
}
@media (min-width: 1640px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 1500px !important;
  }
}
.site-main > *.alignfull,
.site-main > .post > *.alignfull {
  max-width: 100%;
}
.site-main > ul, .site-main > ol,
.site-main > .post > ul,
.site-main > .post > ol {
  padding-left: 40px !important;
}